<template>
    <form @submit.prevent="AddIngreso" >
              <div>

                <select 
                name="fuente" 
                id="disabledSelect"
                class="form-select"
                
                v-model="ingreso.fuente"
                >
                    <option value="0" disabled selected hidden >Seleccione una fuente</option>
                    <option value="NEQUI">NEQUI</option>
                    <option value="BANCOLOMBIA">BANCOLOMBIA</option>
                    <option value="EFECTIVO">EFECTIVO</option>
                </select>
                               
                   <input
                      type='date'
                      name='fecha'
                      class='form-control mt-3'
                      placeholder='Fecha'
                      autoComplete='off'
                      v-model="ingreso.fecha"
                  /> 
              
                  
                  <input
                      type='text'
                      name='descripcion'
                      class='form-control mt-3'
                      placeholder='Descripcion'
                      autoComplete='off'
                      v-model="ingreso.descripcion"
  
                  />
                   <input
                      type='number'
                      name='monto'
                      class='form-control mt-3'
                      placeholder='Monto'
                      autoComplete='off'
                      v-model="ingreso.monto"
  
                  />
                  
  
                  <button 
                  class='btn btn-primary form-control  mt-2'
                  type='submit'  
                  :disabled="getIsLoading"
                  >
                  Guardar
  
                  </button>
  
              </div>
          </form>
  </template>
  
  <script>
  import { mapActions, mapGetters } from 'vuex'
  import Swal from "sweetalert2";
  
  export default {
      data() {
          return{
              ingreso:{}
          }
      },
      computed:{
          ...mapGetters('ferneyIngreso',['getIsLoading','getFuentesIngresos'])
      },
      methods:{
          ...mapActions('ferneyIngreso',['addIngreso']),
          async AddIngreso(){
            if(this.ingreso._id == 0){
                Swal.fire({
                    position:'center',
                    title:'Selecciona una fuente',
                })
            }else{
                var ingresoAdd = {
                    fecha: this.ingreso.fecha,
                    descripcion: this.ingreso.descripcion,
                    monto: this.ingreso.monto,
                    fuente : this.ingreso.fuente
                }
                await this.addIngreso(ingresoAdd)
            }
          }
      },
      created(){
        this.ingreso.fuente = 0
      }
  }
  </script>
  
  <style>
  
  </style>